import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './action'
import * as getters from './getter'

Vue.use(Vuex);

const state = {
    data: [],
    odata: {}
};

const mutations = {
    INIT_NEW(state, data) {
        state.data = data
    },
    INIT_ONE(state, data) {
        console.log(data)
        state.odata = data
    }
};

export default new Vuex.Store({
    state,
    mutations,
    getters,
    actions
});


