<template>
    <div class="cls">
        <search></search>
        <div class="main">
            <div class="link"><router-link to="/classes">所有分类</router-link><span></span><router-link to="/classes">{{getNew[0].classes}}</router-link></div>
            <div class="menu">
                <ul>
                    <li><a v-popover:foo >排序<span></span></a></li>
                    <li class="change"><span style="backgroundColor: #ccc" v-on:click = 'change'>大图</span><span v-on:click = 'change'>列表</span></li>
                </ul>
                   <popover name="foo" :pointer="false" :width="150">
                        <ul class="down">
                            <li><a>销量</a></li>
                            <li><a>最新</a></li>
                            <li><a>店铺</a></li>
                            <li><a>作者</a></li>
                        </ul>
                    </popover>
                    
                
            </div>
            <ul class="items" v-if="isbig">
                <li v-for="item in getNew" :key="item.id"><goods :goodsData="item"></goods></li>
            </ul>
            <ul class="items" v-else>
                <li v-for="item in getNew" :key="item.id"><ranks :rankData="item"></ranks></li>
            </ul>
            
        </div>
    </div>
</template>

<script>
import goods from './goodsDtl'
import ranks from './rankDtl'
import search from './search'
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'gclass',
    components: {
        goods,
        search,
        ranks
    },
    data() {
        return {
            isbig: true
        }
    },
    computed: {
        ...mapGetters([
            'getNew'
        ])
    },
    methods: {
        ...mapActions([
            'postNew',
            'getNewData'
        ]),
        change(event) {
            if (event.path[0].style.backgroundColor == ''){
                if(event.path[1].children[0].style.backgroundColor == '') {
                    event.path[1].children[0].style.backgroundColor = '#ccc'
                    event.path[1].children[1].style.backgroundColor = ''
                    this.isbig = true
                }else {
                    event.path[1].children[1].style.backgroundColor = '#ccc'
                    event.path[1].children[0].style.backgroundColor = ''
                    this.isbig = false
                }
            }
        }

    },
    mounted() {
        this.postNew(this.$route.params.id)
    }
}
</script>

<style lang="scss" scoped>
    .cls {
        margin-top: 100px;
        .main {
            .link {
                padding: 0 30px;
                font-size: 28px;
                font-weight: bold;
                line-height: 50px;
                span {
                    font-weight: bold;
                    display: inline-block;
                    border-right: solid 1px #333;
                    border-bottom: solid 1px #333;
                    width: 15px;
                    height: 15px;
                    transform: rotate(-45deg);
                    margin: 0 15px 0 10px;
                }
                a {
                    border-bottom: 1px #333 solid;
                }
            }
            .items {
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-wrap: wrap;
            }
            .menu {
                padding: 0 20px;
                ul {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    .change {
                        border-radius: 10px;
                        font-size: 28px;
                        overflow: hidden;
                        span {
                            padding: 0 10px;
                            line-height: 32px;
                        }
                        .init {
                            background-color: #ccc;

                        }
                        
                    }
                    li {
                        a {
                            line-height: 60px;
                            font-size: 28px;
                            color: #333;
                            span {
                                font-weight: bold;
                                display: inline-block;
                                border-right: solid 1px #333;
                                border-bottom: solid 1px #333;
                                width: 15px;
                                height: 15px;
                                transform: rotate(45deg);
                                margin: 0 0 5px 10px;
                            }
                        }
                        
                    }
                }
                .down {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    width: 150px;
                    li {
                        a {
                            display: block;
                            width: 150px;
                            text-align: center;
                        }
                        a:hover {
                            background-color: #f3f3f3;
                        }
                    }
                }
                
                
            }
        }  
    }
</style>



