<template>
    <div class="classes">
        <search></search>
        <div class="main">
            <span class="lead">你想找什么样的商品？</span>
            <ul>
               <li v-for="data in listData" :key="data.id">
                   <router-link :to="data.link" class="link">
                       <img :src="data.img" alt="">
                       <span class="name">{{data.name}}</span>
                        <span class="num">{{data.num}}</span>
                   </router-link>
                </li> 
            </ul>
        </div>

    </div>
</template>

<script>
import search from './search'
export default {
    name: 'classes',
    components: {
        search
    },
    data() {
        return {
            listData: [
                {
                    name: '配件饰品',
                    num: '326',
                    img: './static/class.jpg',
                    link: '/gclass/sp'
                },
                {
                    name: '家居良品',
                    num: '326',
                    img: './static/class.jpg',
                    link: '/gclass/jj'
                },
                {
                    name: '文具卡片',
                    num: '326',
                    img: './static/class.jpg',
                    link: '/gclass/wj'
                },
                {
                    name: '鞋子包包',
                    num: '326',
                    img: './static/class.jpg',
                    link: '/gclass/xz'
                }
            ]
        }
    }
}
</script>

<style lang="scss" scoped>
    .classes {
        margin: 100px 0 0 0;
        .main {
            margin: 30px 20px;
            .lead {
                font-size: 32px;
                font-weight: bold;
                line-height: 72px;
            }
            ul {
                width: 710px;
                box-shadow: 1px 1px 1px 1px #ccc;
                border-radius: 8px;
                li {
                    border-bottom: 1px solid #ccc;
                    .link {
                        width: 710px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        img {
                            width: 100px;
                            height: 100px;
                            border-radius: 50px;
                            margin: 15px 20px;
                        }
                        .name {
                            font-weight: bold;
                            font-size: 28px;
                            padding-right: 380px;
                        }
                        .num {
                            padding-right: 30px;

                        }
                    }
                    
                }
            }
        }
    }
</style>



