<template>
    <router-link :to=link>
    <div class="card">
        <div class="image">
            <span></span>
            <img :src="rankData.img" alt="">
        </div>
        <div class="detail">
            <div class="name">{{rankData.name}}</div>
            <div class="author"><i>by </i>{{rankData.author}}</div>
            <span class="price"><span>RMB </span><b>{{rankData.price}}</b></span>
        </div>
    </div>
    </router-link>
</template>

<script>
export default {
    name: 'rankDtl',
    props: ['rankData'],
    data() {
        return {
            link: '/gmain/'+this.rankData.id
        }
    }
}
</script>

<style lang="scss" scoped>
    .card {
        display: flex;
        align-items: center;
        justify-content: flex-start;

        width: 750px;
        .image {
            display: flex;
            justify-content:center;
            width: 180px;
            
            img {
                height: 180px;
                overflow: hidden;
            }
        }
        .detail {
            margin: 0 15px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-around;
            height: 180px;
            .name {
                font-size: 28px;
                font-weight: bold;
                
            }
            .price {
                line-height: 64px;
                font-size: 32px;
            }
        }
    }

</style>



