<template>
    <div class="gmain">
        <search></search>
        <div class="name">{{getone.name}}</div>
        <div class="image"><img :src="getone.img" alt=""></div>
        <div class="price"><span>RMB </span>{{getone.price}}</div>
        <div class="buy"><router-link to="">加入购物车</router-link></div>
        <div class="shop">
            <h2>{{getone.shop}}</h2>
            <router-link to="">逛设计馆</router-link>
        </div>
        <div class="info">
            <h2>商品类别</h2>
            <router-link to="/classes">{{getone.classes}}</router-link>
            <h2>数量</h2>
            <span>10件以上</span>
        </div>
        <div class="about">
            <h2>关于这件商品</h2>
            <span>{{getone.info}}</span>
        </div>
    </div>    
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import search from './search'
export default {
    name: 'gmain',
    components: {
        search
    },
    computed: {
        ...mapGetters([
            'getone'
        ])
    },
    methods: {
        ...mapActions([
            'postone',
            'getoneData'
        ])
    },
    mounted() {
        this.postone(this.$route.params.id)
    }
}
</script>

<style lang="scss" scoped>
    .gmain {
        margin-top: 100px;
        padding: 15px;
        .name {
            line-height: 128px;
            font-weight: bold;
            font-size: 32px;
        }
        .image {
            width: 720px;
            img {
                width: 720px;
            }
        }
        .price {
            line-height: 128px;
            font-size: 48px;
            font-weight: bold;
            color: #666;
        }
        .buy {
            a {
                display: block;
                background-color: purple;
                line-height: 96px;
                color: #fff;
                width: 720px;
                text-align: center;
                border-radius: 8px;
            }
            padding-bottom: 30px;
            border-bottom: 1px solid #666;
        }
        .shop {
            display: flex;
            align-items: center;
            justify-content: space-between;
            h2 {
               font-size: 32px;
            }
            a {
                font-size: 32px;
            }
        }
        .info {
            
            span {
                font-size: 28px;
                
            }
            
        }
        .about {
            span {
                font-size: 28px;
            }
        }
        
    }
    h2 {
        font-size: 28px;
        color: #666;    
        line-height: 81px;    
    }
    a {
        border-bottom: 1px solid purple;
        font-size: 32px;
        color: purple;
    }
</style>



