function makeAction(type) {
    return ({dispatch}, ...args) => dispatch(type, ...args)
}

export const getNewData = function({commit}) {
    let res = []
    switch (arguments[1]) {
        case undefined:
            res = initData;
            break;
    
        case 'sp':
            for(let i in initData) {
                if(initData[i].classes == '配件饰品') {
                    res.push(initData[i])
                }
            }
            break;
        case 'jj':
            for(let i in initData) {
                if(initData[i].classes == '家居良品') {
                    res.push(initData[i])
                }
            }
            break;
        case 'wj':
            for(let i in initData) {
                if(initData[i].classes == '文具卡片') {
                    res.push(initData[i])
                }
            }
            break;    
        case 'xz':
            for(let i in initData) {
                if(initData[i].classes == '鞋子包包') {
                    res.push(initData[i])
                }
            }
            break;    
    }
    if(arguments[1] == undefined) {
        
    }
    console.log(res)
    
    commit('INIT_NEW', res)
    
}

export const getoneData = function({commit}) {
    for(let i in initData) {
        if(initData[i].id == arguments[1]){
            commit('INIT_ONE', initData[i])
        }
    }
    
}

export const postNew = makeAction('getNewData')
export const postone = makeAction('getoneData')



