import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import classes from '@/components/classes'
import empty from '@/components/empty'
import gclass from '@/components/gclass'
import gmain from '@/components/gmain'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/classes',
      component: classes
    },
    {
      path: '/empty',
      name: 'empty',
      component: empty
    },
    {
      path: '/gclass/:id',
      component: gclass
    },
    {
      path: '/gmain/:id',
      name: 'gmain',
      component: gmain
    }
  ]
})



