<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="slide in images" :key="slide.id" class="image"><router-link :to="link(slide.id)"><img :src="slide.img" alt=""></router-link></swiper-slide>
        <!-- <div class="swiper-pagination" slot="pagination"></div> -->
    </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    name: 'banner',
    props: ['images'],
    components: {
        swiper,
        swiperSlide
    },
    methods: {
        link(id) {
            return "/gmain/"+id
        }
    },
    data() {
      return {
        swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          autoplay: true,
          loop:true
        }
      }
    }
}
</script>

<style lang="scss" scoped>
    .image {
        height: 500px;
        overflow: hidden;
        img {
            width: 750px;
        }
    }
    
</style>




